.genres-filter-list {
  padding: 0;
  margin: 0;
  display: flex;
}

.genres-filter-list li:not(:last-child) {
  margin-right: 30px;
}

.genres-filter-list p {
  margin: 0;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
}
